<script>
export default {
  data: () => {
    return {
      nama: "Holla",
      plot: [],
      time: '',
      timex: '',
      search: '',
      search2: '',
      tabs: 0,
      tasks: [],
      tabs2: 0,
      tasks2: [],
      stats: [
          {
            actionIcon: 'mdi-alert',
            actionText: 'Get More Space...',
            color: 'success',
            icon: 'mdi-account-group',
            title: 'Student',
            value: '184 murid',
          },
          {
            actionIcon: 'mdi-tag',
            actionText: 'Tracked from Google Analytics',
            color: 'primary',
            icon: 'fa-solid fa-book',
            title: 'Mapel',
            value: '20 mapel',
          },
          {
            actionIcon: 'mdi-calendar-range',
            actionText: 'Last 24 Hours',
            color: '#FD9A13',
            icon: 'fa-solid fa-layer-group',
            title: 'Semester',
            value: '2',
          },
        ],
      nilai : [
        {
          name: "Kuma Nov Revilan",
          mapel : ["Matematika", "Fisika", "kimia","IPS",],
          nilai : ["70","75", "70","78", ],
          avergescore : ["75", "73", "76", "79",],
          score : ["80","98", "87", "91",],
        },
        {
          name: "Nov Revilan",
          mapel : ["Matematika", "Fisika", "kimia","IPS",],
          nilai : ["70","75", "70","78", ],
          avergescore : ["75", "73", "76", "79",],
          score : ["80","98", "87", "91",],
        },
      ],
      headers: [
      {
        sortable: false,
        text: 'ID',
        value: 'id',
      },
      {
        sortable: true,
        text: 'Mapel',
        value: 'mapel',
      },
      {
        sortable: false,
        text: 'Score required',
        value: 'nilailulus',
      },
      {
        sortable: false,
        text: 'Averge Student Score',
        value: 'avergescore',
      },
      {
        sortable: false,
        text: 'Student Score',
        value: 'score',
      },
      {
        sortable: false,
        text: 'Action',
        value: 'action',
      },

      ],
      items: [],
      gap: [],
      pos: [],
      headers2: [
          {
            sortable: false,
            text: 'ID',
            value: 'id',
          },
          {
            sortable: true,
            text: 'Name',
            value: 'name',
          },
          {
            sortable: true,
            text: 'Math',
            value: 'math',
          },
          {
            sortable: true,
            text: 'Al Hadis',
            value: 'alhadis',
          },
          {
            sortable: true,
            text: 'Kimia',
            value: 'kimia',
          },
          {
            sortable: false,
            text: 'Action',
            value: 'action',
          },
        ],
      items2: [],
      gap2: [],
      pos2: [],
      edit (item){
        this.editedIndex = this.gap.indexOf(item)
        this.editedItem = Object.assign({}, item)
        // this.dialog = true
          var x = document.querySelectorAll("tbody")[0].querySelectorAll("tr")
          x[this.gap.indexOf(item)].contentEditable = "true"
      },
      save (item) {
          this.editedIndex = this.gap.indexOf(item)
          this.editedItem = Object.assign({}, item)
          // this.dialogDelete = true
          // Start collection
          var x = document.querySelectorAll("tbody")[0].querySelectorAll("tr")
